$(document).ready(function(){
    var certificateData = window.opener.getCertificateData(); 
    
    $('body').addClass('lang-'+certificateData.language.code+' '+certificateData.language.direction);
    
    $('.CR-title').html(certificateData.text.title[0].Text);
    $('.CR-SName').html(certificateData.studentName);
    $('.CR-cmp').html(certificateData.text.text[0].Text);
    $('.CR-courseName').html(certificateData.courseName);
    $('.CR-Date').html(certificateData.text.date[0].Text.split('{date}').join(certificateData.completionDate));
    $('.CR-ID').html(certificateData.text.courseid[0].Text.split('{courseid}').join(certificateData.courseID));
    
    $('.certificate').show();
		
     var temp=setTimeout(function(){
        window.print(); 
     }, 1000);
});